import { mapState } from "vuex";

export default {
    name: "about",
    data() {
        return {
            coupon: {
                coupon: {
                    min_price: '',
                    rule: ''
                }
            },
            success: false,
            lose: false,
            iphone_x: false,
            exchangeCoupon: false,
            current: 'wechat',
        };
    },
    computed: {
        ...mapState({
            integralImg: state => state.mallConfig.plugin.integral_mall,
            mall: state => state.mallConfig.mall
        }),
    },
    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        let id = options.id;
        that.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        that.getList(id);
        uni.getSystemInfo({
            success: function (res) {
                if(res.model.indexOf('iPhone X') > -1 || res.model.indexOf('iPhone 11') > -1 || res.model.indexOf('iPhone11') > -1 || res.model.indexOf('iPhone12') > -1 || res.model.indexOf('Unknown Device') > -1) {
                    that.iphone_x = true;
                }
            }
        })
    },
    methods: {
        close() {
            this.success = false;
            this.lose = false;
        },

        toList() {
            this.success = false;
            uni.navigateTo({
                url: '/pages/coupon/index/index'
            });
        },
        getList(id) {
            let that = this;
            that.$request({
                url: that.$api.integral_mall.detail,
                data: {
                    id: id
                }
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    that.coupon = response.data.detail;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
        submit() {
            let that = this;
            let id = that.coupon.id;
            that.exchangeCoupon = false;
            if (that.userInfo != null && that.userInfo.integral < that.choose.integral_num) {
                uni.showToast({
                    title: '积分不足',
                    icon: 'none',
                    duration: 1000
                });
            }else {
                uni.showLoading({
                    mask: true,
                    title: '领取中...'
                });
                that.$request({
                    url: that.$api.integral_mall.coupon_submit,
                    data: {
                        id: id
                    },
                    method: 'post'
                }).then(response=>{
                    if(response.code == 0) {
                        let queue_id = response.data.queue_id;
                        let token = response.data.token;
                        that.getPayOrderId(queue_id, token)
                    }else {
                        uni.hideLoading();
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(response => {
                    uni.hideLoading();
                });
            }
        },

        getPayOrderId(queue_id, token) {
            this.$request({
                url: this.$api.integral_mall.coupon_pay,
                data: {
                    queue_id: queue_id,
                    token: token,
                },
            }).then(response => {
                if (response.code === 0) {
                    if (response.data.retry && response.data.retry === 1) {
                        this.getPayDataTimer = setTimeout(() => {
                            this.getPayOrderId(queue_id, token);
                        }, 1000);
                    } else {
                        uni.hideLoading();
                        this.pay(response.data.id);
                    }
                } else {
                    this.exchangeCoupon = false;
                    uni.hideLoading();
                    uni.showModal({
                        title: '提示',
                        content: response.msg,
                        showCancel: false,
                    });
                }
            }).catch(e => {
                this.exchangeCoupon = false;
                uni.hideLoading();
                uni.showModal({
                    title: '提示',
                    content: e.errMsg,
                    showCancel: false,
                });
            });
        },
        pay(id) {
            this.$payment.pay(id).then(res => {
                this.success = true;
            }).catch(e => {
                uni.showToast({
                    title: '支付失败',
                    icon: 'none',
                    duration: 1000
                });
            });
        },
    }
}
